import React, { useEffect } from "react";
import { Container } from "react-bootstrap";
// needed styles
import styles from "./index.module.scss";
// needed components
// assets
import HeroSlider from "./HeroSlider/HeroSlider";
import WhiteCard from "components/shared/WhiteCard/WhiteCard";
import ProgramDetailsTable from "./ProgramDetailsTable/ProgramDetailsTable";
import ProgramDetails from "./ProgramDetails/ProgramDetails";
import PriceTable from "./PriceTable/PriceTable";
import { useDispatch, useSelector } from "react-redux";
import { Link, useParams } from "react-router-dom";

import {
  getAllTrips,
  getSingleHajjTrip,
  getSingleSpecialTrip,
  getSingleUmraTrip,
} from "store/trips/slice";

const SpecialSingleTripPage = () => {
  const { tripType, tripId } = useParams();
  const dispatch = useDispatch();

  useEffect(() => {
    dispatch(getSingleSpecialTrip(tripId));
  }, []);

  const { singleTrip, loading } = useSelector((state) => state.trips) || {};
  const {
    pictures,
    brief_ar,
    brief_en,
    airline_ar,
    airline_en,
    duration,
    end_booking_date,
    start_booking_date,
    price_per_person,
    rating,
    title_ar,
    title_en,
  } = singleTrip || {};
  return (
    <Container fluid className={styles["wrapper"]}>
      <Container className={styles["container"]}>
        <div className={styles["header-wrapper"]}>
          <div>
            <h2 className={styles["slider-header"]}> {title_ar}</h2>
            <h3 className={styles["slider-sub-header"]}> {brief_ar}</h3>
          </div>

          <div className={styles["booking"]}>
            <Link to={`/bookTrip/${tripId}`}>احجز الرحله</Link>
          </div>
        </div>
        {/* </div> */}
        <HeroSlider data={pictures} />
        {/* <WhiteCard>{desc_ar}</WhiteCard> */}
        <ProgramDetailsTable singleTrip={singleTrip} />

        <PriceTable singleTrip={singleTrip} />
      </Container>
    </Container>
  );
};

export default SpecialSingleTripPage;
