import WhiteCard from "components/shared/WhiteCard/WhiteCard";
import React from "react";
import { v4 } from "uuid";

// needed asssets

// svgs
// import { ReactComponent as Search } from "assets/svgs/search.svg";

// images

// needed styles
import styles from "./index.module.scss";

const PriceTable = ({ singleTrip }) => {
  const {
    start_booking_date,

    end_booking_date,
    price_per_person,
  } = singleTrip || {};
  const tableData = [
    {
      header: " ",
      body: `من ${start_booking_date}    الي ${end_booking_date}`,
    },
    {
      header: "سعر الفرد",
      body: `${price_per_person}`,
    },
  ];
  return (
    //
    <WhiteCard>
      <div className={styles["table"]}>
        <div className={styles["table-header"]}>الاسعار :</div>
        <div className={styles["table-body"]}>
          <table>
            {tableData?.map((tableItem) => (
              <tr key={v4()}>
                <th>{tableItem?.header}</th>
                <td>{tableItem?.body}</td>
              </tr>
            ))}
          </table>
        </div>
      </div>
    </WhiteCard>
  );
};

export default PriceTable;
