import React from "react";
import { Swiper, SwiperSlide } from "swiper/react";
import { v4 } from "uuid";

// needed asssets

// svgs
// import { ReactComponent as Search } from "assets/svgs/search.svg";

// images

// needed styles
// import styles from "./index.module.scss";

const HeroSlider = ({ data }) => {
  return (
    //
    <div className={"single-umrah-slider"}>
      <Swiper
        slidesPerView={1}
        loop={true}
        pagination={{
          clickable: true,
        }}
        navigation={true}
        className="mySwiper"
      >
        {data?.map((image) => (
          <SwiperSlide key={v4()}>
            <div>
              <img
                src={image}
                alt="imageasd"
                width={"100%"}
                height={"572px"}
                style={{ objectFit: "cover" }}
              />
            </div>
          </SwiperSlide>
        ))}
      </Swiper>
    </div>
  );
};

export default HeroSlider;
