import React from "react";
import { v4 } from "uuid";

// needed asssets

// svgs
// import { ReactComponent as Search } from "assets/svgs/search.svg";

// images

// components
import WhiteCard from "components/shared/WhiteCard/WhiteCard";

// needed styles
import styles from "./index.module.scss";

const ProgramDetailsTable = ({ singleTrip }) => {
  const {
    brief_ar,
    brief_en,
    airline_ar,
    airline_en,
    duration,
    end_booking_date,
    start_booking_date,
    price_per_person,
    rating,
    title_ar,
    title_en,
  } = singleTrip || {};
  const tableData = [
    {
      header: "تاريخ الحجز",
      body: `من ${start_booking_date}    الي ${end_booking_date}`,
      // body: {
      //   to: "22/7/2022",
      //   from: "22/7/2022",
      // },
    },
    {
      header: "نبذه عن",
      body: `${brief_ar}`,
    },
    {
      header: "التقييم",
      body: `${rating} / 5`,
    },
    {
      header: "  مدة الرحلة ",
      body: `${duration}`,
    },
  ];

  return (
    <WhiteCard>
      <h3>البرنامج :</h3>

      <div className={styles["table"]}>
        <div className={styles["table-header"]}>بيانات الرحله</div>
        <div className={styles["table-body"]}>
          <table>
            {tableData?.map((tableItem) => (
              <tr key={v4()}>
                <th>{tableItem?.header}</th>
                <td>{tableItem?.body}</td>
                {/* {typeof tableItem?.body === "object" ? (
                  <>{renderTableObject(tableItem?.body)}</>
                ) : (
                  <>
                  </>
                )} */}
              </tr>
            ))}
          </table>
        </div>
      </div>
    </WhiteCard>
  );
};

export default ProgramDetailsTable;
